@import url(https://cdn.jsdelivr.net/npm/bootstrap@5.0.0-beta2/dist/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* SCSS RGB 
$blue-crayola: rgba(96, 128, 255, 1);
$sky-blue-crayola: rgba(128, 236, 255, 1);
$light-gray: rgba(200, 214, 215, 1);
$space-cadet: rgba(33, 41, 92, 1);
$russian-violet: rgba(13, 6, 48, 1); */

a, a:hover {
  text-decoration: none;
  color: inherit;
}

.active {
  border-bottom: 2px solid rgba(13, 6, 48, 1);
}

h1 {
  color: rgba(33, 41, 92, 1);
  text-shadow: 0 10px 5px rgba(18, 0, 66, 0.5);
  font-weight: bold;
  padding-bottom: .5em;
}

.App {
  text-align: center;
  position: relative;
  min-height: 100vh;
}

.App-header, .App-footer {
  background-color: #282c34;
  background: linear-gradient(0deg, rgba(152, 136, 211, 1) 20%, rgba(58,52,126,1) 46%);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header img {
  max-width: 100%;
}

.homepage-title img {
  height: 10em;
}

.App-footer {
  background: linear-gradient(0deg, rgba(58,52,126,1) 30%,  rgba(152, 136, 211, 1) 75%);
  height: 2.5em;
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
} 

.App-link {
  color: #61dafb;
}

.nav-item {
  font-family: "Akaya Telivigala", cursive;
  font-size: 2em;
  color: rgba(4, 0, 37, 1);
  transition-property: all;
  transition-duration: .45s;
  margin: 2px, 15px;
}

.nav-item:hover {
  font-size: 2.2em;
  /* background: linear-gradient(0deg, rgba(40, 44, 52, .5) 10%, rgba(108,56,170, .5) 50%, rgba(40, 44, 52, .5) 90%) */
  text-shadow: 5px 5px 5px rgba(18, 0, 66, 0.5);
}

.page {
  min-height: 50vh ;
  background: rgb(220, 220, 255);
  padding: 1.5em 1.5em 5em 1.5em;
}

.span-emph {
  color: rgba(13, 6, 48, 1);
  font-weight:bold;
}

.icon-class {
  height: 2em;
}

.card-title {
  background: rgb(220, 220, 255);
  color: white;
}

.project-img {
  width: 100%;
}

.form-label {
  width: 15%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

